.blogContainer {
    gap: 32px;
}

.tagButtContainer {
    max-width: 800px;
    gap: 12px;
    display: flex;
    flex-wrap: wrap;
    justify-content: unset;
}

.tagButtContainer>* {
    user-select: none;
    padding: 12px 16px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;
    transition: all .2s;
}

.tagButtContainer>*:hover {
    color: var(--neutral-3);
}

.activeTag {
    background-color: var(--neutral-2);
    color: var(--neutral-6) !important;
}

.blog>img {
    width: 200px;
    border-radius: 4px;
}

.blog {
    max-width: 800px;
    border-radius: 8px;
    width: 100%;
    padding: 24px;
    background-color: rgb(32, 32, 32);
    transition: background-color .25s ease-out;
    gap: 16px;
}

.blog:hover {
    background-color:rgb(54, 54, 54);
}

.blogLeft {
    gap: 24px;

}

.tagComponentsContainer {
    gap: 12px;
    display: flex;
    align-items: flex-start;
}

.tagComponentsContainer>* {
    padding: 4px 8px;
    border-radius: 2px;
    font-size: 14px;
}

.blogDate {
    margin-bottom: -6px;
    font-size: 12px;
    color: var(--neutral-4);
    font-weight: 400;
}

.HTML {
    color: rgb(172, 78, 2);
    background-color: rgb(255, 220, 190);
}

.JavaScript {
    color: rgb(102, 96, 14);
    background-color: rgb(255, 250, 187);
}

.CSS {
    color: rgb(34, 68, 182);
    background-color: rgb(194, 210, 255);
}

.AWS {
    color: rgb(131, 96, 14);
    background-color: rgb(255, 236, 208);
}

.React {
    color: rgb(5, 97, 151);
    background-color: rgb(194, 225, 255);
}

.MongoDB {
    color: rgb(5, 151, 24);
    background-color: rgb(196, 255, 201);
}

.Node {
    color: rgb(95, 151, 5);
    background-color: rgb(211, 255, 190);
}

/* BLOG POST */
.BlogPost-navigation {
    margin-bottom: -40px;
    width: fit-content;
    gap: 6px;
    align-items: flex-start;
}
.BlogPost-navigation > p {
    font-size: 20px;
}
.BlogPost-goback > p {
    font-size: 20px;
    color: rgb(66,166,255);
}

.BlogPost-navigation>a>p:hover {
    text-decoration: underline;
}

.BlogPost-hero {
    padding: 50px;
    position: relative;
    overflow: hidden;
    justify-content: space-between;
    gap: 150px;
    border-radius: 20px;
}

.BlogPost-hero>img {
    filter: brightness(30%);
    position: absolute;
    inset: -9999px;
    margin: auto;
    min-width: 100%;
    min-height: 100%;
    z-index: -1;
}

.BlogPost-hero>p,
.BlogPost-title,
.BlogPost-hero-bot>p {
    color: var(--neutral-5);
}

.BlogPost-hero-bot {
    gap: 24px;
    max-width: 1200px;
}

.BlogPost-hero-bot>p {
    color: var(--neutral-4);
    max-width: 700px;
}

.BlogPost-hero-bot>.tagComponentsContainer {
    width: fit-content;
}

.BlogPost-stats {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    border: 1px solid var(--neutral-2);
    border-radius: 20px;
    padding: 32px 16px;
}

.BlogPost-stat {
    width: 100%;
    gap: 8px;
    text-align: center;
}

.BlogPost-body {
    padding-bottom: 100px;
    max-width: 800px;
    margin-top: 50px;
    gap: 32px;
    margin: auto;
}
.BlogPost-body img {
    max-width: 800px;
    width: 100%;
    margin: 25px 0;
    box-shadow: var(--shadow-l);
}
.BlogPost-body a > p {
    color: blue;
}
.BlogPost-body a:hover {
    text-decoration: underline;
}
@media screen and (max-width: 1000px) {
    .BlogPost-hero  {
        padding: 50px 32px;
    }
}
@media screen and (max-width: 600px) {
    .BlogPost-hero  {
        padding: 32px;
    }
    .BlogPost-title {
        font-size: 28px;
    }
    .blog {
        flex-direction: column;
        align-items: unset;
        padding: 32px;
        gap: 32px;
    }
    .blog > img {
        width: 100%;
    }
    .blogLeft {
        gap: 16px;
    }
}