/* PROJECTS */
.projectsContainer>.project:first-child {
    border: none;
}

.projectsContainer>.project:first-child>* {
    font-size: 12px;
    color: var(--neutral-3);
    font-weight: 400;
}

/* PROJECT */
.project {
    border-top: 1px solid var(--neutral-2);
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    align-items: flex-start;
    justify-content: flex-start;
    padding: 32px 0;
    width: 100%;
}

.project>* {
    width: 100%;
}

.project>.h6,
.project>h6 {
    position: relative;
    align-items: flex-start;
    gap: 16px;
    justify-content: flex-start;
    font-weight: 500;
    font-size: 24px;
    grid-column: span 3;
}

.project>.tools {
    grid-column: span 2;
}

.project>.responsibilities {
    grid-column: span 2;
}

.project>.date {
    grid-column: span 1;
}

.projectPadding {
    padding: 32px 24px;
    gap: 24px;
}

.projectHover {
    width: 100%;
}

.projectPadding>* {
    white-space: normal;
    transition: transform .5s, opacity .35s;
}

.AProjectCont:hover .projectPadding>* {
    opacity: .5;
    transform: translateX(17px);
}

.AProjectCont:hover .projectPadding>.h6 {
    transform: translateX(-17px);
}

/* HOVER EFFECT */
.Aproject {
    position: relative;
    background-color: var(--neutral-1);
}   

.AProjectCont {
    position: relative;
    z-index: 9;
    transition: margin-bottom .7s;
}

.AProjectCont:hover {
    margin-bottom: var(--image-height)
}

.projectImage {
    pointer-events: default;
    width: 100%;
    position: absolute;
    z-index: -1;
    top: 100%;
}

:root {
    --image-height: 0;
}
